header {
    background-color: #8EB437;
    width: 100;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.6s;
    padding: 20px 20px 20px 100px;
    z-index: 10000;
    margin: 0  0 20px 0 ;
    .logo {
        position: relative;
        font-weight: 700;
        color: #fff;
        text-decoration: none;
        font-size: 2em;
        text-transform: uppercase;
        letter-spacing: 2px;
        transition: 0.6s;
    }
    ul {
        margin-right: 5px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;        
        height: 50px;
        p{
            color: white;
        }
        li {
            cursor: pointer;
            height: 50px;
            line-height: 50px;
            vertical-align: bottom;
            border: none;
            border-color: white;
            &:hover{
                border-bottom: 2px solid white;
                transition: 0.5s;
                transition-timing-function: linear;
            }
            position: relative;
            list-style: none;
            a,p {
                position: relative;
                margin: 0 15px;
                text-decoration: none;
                color: #fff;
                letter-spacing: 2px;
                font-weight: 500;
                transition: 0.6s;
            }
        }
    }
}
header.sticky {
    padding: 5px 100px;
    background: #fff;
  }

